
import { defineComponent, onMounted, reactive, registerRuntimeCompiler } from 'vue'
import { CompressOutlined } from '@ant-design/icons-vue'
export default defineComponent({
  props: {
    title: {
      type: String,
      default: '标题'
    },
    desc: {
      type: Object,
      default: () => {
        return {
          title: 'Label',
          value: 'value'
        }
      }
    }
  },
  components: {
    CompressOutlined
  },
  setup(props) {
    const prop = reactive(props)
    const state = reactive({
      date:''
    })
    onMounted(()=>{
      state.date = 'asdsada'
    })
    return { prop, state}
  }
})
